
var andonDetailView = {

	init: function() {
		this.loadPage();
		this.event();
	},
	event: function() {
		setTimeout(function(){
			$.each($("a[type='andon']"),function(index,item){
				var el = $(item);
				if(el.attr("statusCode") == 2){
					el.css("background-color",el.parents(".tab").attr("touchColor"));
					el.attr("statusCode") == 1;
				}else{
					el.css("background-color",el.parents(".tab").attr("normalColor"));
					el.attr("statusCode") == 1;
				}
				
			})

			$("a[type='andon']").on("click",function(e){
				var el = $(e.target);
				if(e.target.tagName != "A"){
					el = el.parents("a");
				}

				if(el.attr("statusCode") == 2){
					console.log(el.parents(".tab").attr("touchColor"));
					el.css("background-color",el.parents(".tab").attr("normalColor"))
					el.attr("statusCode",1);
				}else{
					el.css("background-color",el.parents(".tab").attr("touchColor"));
					el.attr("statusCode",2);
				}
				
			})
		},100)
		
	},
	loadPage:function(callback){
		$.getJSON(app.xhrUrl+"andondata.json", function(data){
			app.tplManager('andonTpl',data,'andonRender');
		});
	},
	render : function(config){
	}
}

F7.onPageInit('andonDetail', function (page) {
	$(document).off("keydown");		// 别的页面去除主页的菜单快捷键
	andonDetailView.init();
});



